import PNotify from 'pnotify/dist/es/PNotify.js';
import 'pnotify/dist/PNotifyBrightTheme.css';
import countryListOneTemplate from '../templates/country-list-one.hbs';
import countryListItemsTemplate from '../templates/country-list-item.hbs';
import { refs } from './country';

// const refs = {
//   countryList: document.querySelector('#country-list'),
// };

export default function buildMarkup(data) {
  console.log(data);
  if (data.length > 10) {
    const notice = PNotify.notice({
      text: 'Too many matches found. Please enter a more specific query!',
      modules: {
        Buttons: {
          closer: false,
          sticker: false,
        },
      },
    });
    notice.on('click', function () {
      notice.close();
    });
  } else if (data.length > 2 && data.length < 10) {
    // const buildListItems = data.reduce(
    //   (acc, el) => acc + countryListItemsTemplate(el),
    //   '',
    // );
    clearInputForm();
    const buildListItems = countryListItemsTemplate(data);

    console.log(data);
    refs.countryList.insertAdjacentHTML('beforeend', buildListItems);
  } else if (data.length === 1) {
    const buildListOne = data.reduce(
      (acc, el) => acc + countryListOneTemplate(el),
      '',
    );
    clearInputForm();
    refs.countryList.insertAdjacentHTML('beforeend', buildListOne);
  }
}

function clearInputForm() {
  refs.countryList.innerHTML = '';
}
